@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

.App {
  text-align: center;
}

.App .App-header {
  background-color: #282c34;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.App .App-header #quote-box {
  line-height: 50px;
  font-weight: 700;
  font-size: 40px;
  background-color: #fff;
  border-radius: 10px;
  color: #282c34;
  min-height: 40vh;
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 20px;
}

.App .App-header #quote-box #author {
  text-align: end;
}

.App .App-header #quote-box .tweet-new {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
}

.App .App-header #quote-box .tweet-new #new-quote {
  width: 150px;
  text-align: center;
  -ms-grid-row-align: center;
  align-self: center;
  padding: 10px 20px;
  background-color: #282c34;
  color: #fff;
  border-radius: 5px;
  outline: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.App .App-header #quote-box .tweet-new #tweet-quote {
  font-size: 25px;
  text-align: center;
  background-color: #282c34;
  color: #fff;
  width: 60px;
  height: 43px;
  line-height: 43px;
  border-radius: 5px;
}

@media (max-width: 601px) {
  .App .App-header #quote-box {
    min-height: 50vh;
    width: 80vw;
  }
}

